<template>
	<div class="main-area">
		<section class="album-section" v-if="playlists.length">
			<div class="album-section-title">
				<h3>Playlists</h3>
			</div>
			
			<div v-for="(playlist, i ) in playlists" :index="i" :key="playlist.i">
				<div class="album-items">
					<div class="album-footer">
						<router-link :to="{ name: 'PlaylistItem', params: { id: (i+1) }}">
							<h4>{{i+1}}. {{playlist[0].name}}</h4>
						</router-link>
					</div>
				</div>
			</div>
		</section>
		<section class="album-section" v-else>
			<div class="album-section-title">
				<h3>You haven't a playlist yet</h3>
			</div>
		</section>
	</div>
</template>
<script>
export default {
	name: 'Playlist',
	data: function(){
		return{
			playlists: [],
		}
	},
	created: function(){

		for (let i = 0; i < window.localStorage.length; i++) {
			let key = window.localStorage.key(i);
			if (key.slice(0,5) === "pl_mb") {
				let playlist_name = key.split(':')
				this.playlists.push(
					[{'name': playlist_name[1]},{'track': JSON.parse(window.localStorage.getItem(key))}]
				);
			}
		}
	}
}
</script>
<style scoped>
.flex{
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
}
</style>